// src/component/blogService/LikeShareSidebar.tsx
'use client';

import React, { useState, useEffect } from 'react';
import styles from '@/styles/blogService/postDetail.module.css';
import { toggleLike } from '@/api/blogService/blog'; // API Ìï®Ïàò import

interface LikeShareSidebarProps {
  postId: number;
  initialLikeCount?: number; // ÎÇòÏ§ëÏóê Î∂ÄÎ™®Î°úÎ∂ÄÌÑ∞ Î∞õÏùÑ Ï¥àÍ∏∞ Ï¢ãÏïÑÏöî Ïàò
  initialIsLiked?: boolean;  // ÎÇòÏ§ëÏóê Î∂ÄÎ™®Î°úÎ∂ÄÌÑ∞ Î∞õÏùÑ Ï¥àÍ∏∞ Ï¢ãÏïÑÏöî ÏÉÅÌÉú
}

export default function LikeShareSidebar({
                                           postId,
                                           initialLikeCount = 0,
                                           initialIsLiked = false
                                         }: LikeShareSidebarProps) {

  const [likeCount, setLikeCount] = useState(initialLikeCount);
  const [isLiked, setIsLiked] = useState(initialIsLiked);
  const [isLoading, setIsLoading] = useState(false);

  // (ÏÑ†ÌÉù ÏÇ¨Ìï≠) propsÎ°ú Ï¥àÍ∏∞Í∞íÏù¥ ÎÇòÏ§ëÏóê Îì§Ïñ¥Ïò¨ Í≤ΩÏö∞Î•º ÎåÄÎπÑÌï¥ ÏÉÅÌÉú ÎèôÍ∏∞Ìôî
  useEffect(() => {
    setLikeCount(initialLikeCount);
    setIsLiked(initialIsLiked);
  }, [initialLikeCount, initialIsLiked]);

  const handleLike = async () => {
    // 1. Î°úÍ∑∏Ïù∏ ÌôïÏù∏
    const userSignId = localStorage.getItem('userSignId');
    if (!userSignId) {
      alert("Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌïú ÏÑúÎπÑÏä§ÏûÖÎãàÎã§.");
      return;
    }

    if (isLoading) return; // Ï§ëÎ≥µ ÌÅ¥Î¶≠ Î∞©ÏßÄ
    setIsLoading(true);

    try {
      // 2. API Ìò∏Ï∂ú
      const result = await toggleLike(postId, userSignId);

      // 3. ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏ (ÏÑúÎ≤Ñ ÏùëÎãµÍ∞íÏúºÎ°ú Í∞±Ïã†)
      setIsLiked(result.isLiked);
      setLikeCount(result.likeCount);

    } catch (error) {
      console.error("Ï¢ãÏïÑÏöî Ïã§Ìå®:", error);
      alert("Ï¢ãÏïÑÏöî Ï≤òÎ¶¨Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
    } finally {
      setIsLoading(false);
    }
  };

  const handleShare = () => {
    const shareUrl = `${window.location.origin}/post/${postId}`;
    navigator.clipboard.writeText(shareUrl)
    .then(() => alert('Í≤åÏãúÎ¨º ÎßÅÌÅ¨Í∞Ä ÌÅ¥Î¶ΩÎ≥¥ÎìúÏóê Î≥µÏÇ¨ÎêòÏóàÏäµÎãàÎã§.'))
    .catch(err => console.error('ÌÅ¥Î¶ΩÎ≥¥Îìú Î≥µÏÇ¨ Ïã§Ìå®:', err));
  };

  return (
      <div className={styles.reactionContainer}>
        <button
            onClick={handleLike}
            className={`${styles.likeButton} ${isLiked ? styles.activeLike : ''}`} // Ï¢ãÏïÑÏöî ÏÉÅÌÉúÏùº Îïå Ïä§ÌÉÄÏùº Ï∂îÍ∞Ä Í∞ÄÎä•
            title={isLiked ? "Ï¢ãÏïÑÏöî Ï∑®ÏÜå" : "Ï¢ãÏïÑÏöî"}
            disabled={isLoading}
        >
          {/* Ï¢ãÏïÑÏöî ÏÉÅÌÉúÏóê Îî∞Îùº ÏïÑÏù¥ÏΩò Î≥ÄÍ≤Ω (Ï±ÑÏõåÏßÑ ÌïòÌä∏ / Îπà ÌïòÌä∏) */}
          {isLiked ? '‚ù§Ô∏è' : 'ü§ç'}
          <span>{likeCount}</span>
        </button>

        <button
            onClick={handleShare}
            className={styles.shareButton}
            title="Í≥µÏú† ÎßÅÌÅ¨ Î≥µÏÇ¨"
        >
          üîó
        </button>
      </div>
  );
}