'use client';

import { useEffect, useRef } from 'react'; // üí° useRef ÏûÑÌè¨Ìä∏

export default function ProgressBar() {
  // üü¢ 1. useRefÎ•º ÏÇ¨Ïö©ÌïòÏó¨ DOM ÏöîÏÜåÎ•º Ï∞∏Ï°∞Ìï† Î†àÌçºÎü∞Ïä§Î•º ÏÉùÏÑ±
  const progressBarRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const updateProgress = () => {
      const scrolled = window.scrollY;
      const height = document.documentElement.scrollHeight - window.innerHeight;
      const progress = height > 0 ? (scrolled / height) * 100 : 0;

      if (progressBarRef.current) {
        // document.getElementById() ÎåÄÏã† ref.current ÏÇ¨Ïö©
        progressBarRef.current.style.width = `${progress}%`;
      }
    };

    updateProgress();

    window.addEventListener('scroll', updateProgress);
    return () => window.removeEventListener('scroll', updateProgress);
  }, []);

  return (
      <div className="fixed top-0 left-0 w-full h-1 bg-gray-200 z-50 pointer-events-none">
        <div
            ref={progressBarRef} // üü¢ 3. ÏÉùÏÑ±Ìïú refÎ•º ÏöîÏÜåÏóê Ïó∞Í≤∞
            id="progress-bar" // IDÎäî Ïú†ÏßÄÌï¥ÎèÑ Î¨¥Î∞©ÌïòÏßÄÎßå, useRefÍ∞Ä Ï£º Ï†ëÍ∑º Î∞©ÏãùÏù¥ Îê©ÎãàÎã§.
            className="h-full bg-green-500 transition-all duration-150"
            style={{ width: '0%' }}
        />
      </div>
  );
}