// src/component/blogService/PostDetail.tsx

'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import LikeShareSidebar from './LikeShareSidebar';
import ProgressBar from './ProgressBar';
import TableOfContents from './TableOfContents';
import PostContent from './PostContent';
import CommentSection from './CommentSection'; // Í∏∞Ï°¥ import Ïú†ÏßÄ
import { PostDetailDTO } from '@/types/blogService/blogType';
import { deleteFeed, getPostTags,incrementViewCount } from '@/api/blogService/blog';
import styles from '@/styles/blogService/postDetail.module.css';

// HTML ÎÇ¥Ïö©Ïóê Ï†úÎ™© ÌÉúÍ∑∏(H1, H2, H3 Îì±)Í∞Ä Ìè¨Ìï®ÎêòÏñ¥ ÏûàÎäîÏßÄ ÌôïÏù∏ÌïòÎäî Ìï®Ïàò
const hasHeadings = (htmlContent: string): boolean => {
  if (!htmlContent) return false;
  return /<h[1-3]/i.test(htmlContent);
};

export default function PostDetail({ post }: { post: PostDetailDTO }) {
  const router = useRouter();
  const [currentUserSignId, setCurrentUserSignId] = useState('');
  const [isDeleting, setIsDeleting] = useState(false);
  const [tags, setTags] = useState<string[]>([]);

  const isAuthor = currentUserSignId === post.authorId;
  const shouldShowToc = hasHeadings(post.content);

  useEffect(() => {
    const userId = localStorage.getItem('userSignId') || '';
    setCurrentUserSignId(userId);

    const updateViewCount = async () => {
      try {
        await incrementViewCount(post.postId);
        console.log(`Í≤åÏãúÎ¨º ID ${post.postId} Ï°∞ÌöåÏàò Ï¶ùÍ∞Ä ÏÑ±Í≥µ`);
      } catch (error) {
        console.error('Ï°∞ÌöåÏàò Ï¶ùÍ∞Ä Ïã§Ìå®:', error);
      }
    };
    updateViewCount();

    if (post.tags && Array.isArray(post.tags) && post.tags.length > 0) {
      setTags(post.tags);
    } else {
      const fetchTags = async () => {
        try {
          const fetchedTags = await getPostTags(post.postId);
          setTags(fetchedTags);
        } catch (error) {
          console.error('ÌÉúÍ∑∏ Í∞ÄÏ†∏Ïò§Í∏∞ Ïã§Ìå®:', error);
          setTags([]);
        }
      };
      fetchTags();
    }
  }, [post]);

  const handleEdit = () => {
    router.push(`/post/edit/${post.postId}`);
  };

  const handleDelete = async () => {
    if (window.confirm('Ï†ïÎßêÎ°ú Ïù¥ Í≤åÏãúÎ¨ºÏùÑ ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?')) {
      setIsDeleting(true);
      try {
        await deleteFeed(post.postId, currentUserSignId);        alert('Í≤åÏãúÎ¨ºÏù¥ ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.');
        router.push('/');
      } catch (error) {
        console.error('ÏÇ≠Ï†ú Ïã§Ìå®:', error);
        alert('Í≤åÏãúÎ¨º ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
      } finally {
        setIsDeleting(false);
      }
    }
  };

  return (
      <>
        <ProgressBar />

        <div className={styles.detailContainer}>
          <header className={styles.postHeader}>
            <h1 className={styles.title}>{post.title}</h1>

            {/* ‚úÖ [ÏàòÏ†ï] ÏûëÏÑ±Ïûê Ï†ïÎ≥¥ÏôÄ ÏàòÏ†ï/ÏÇ≠Ï†ú Î≤ÑÌäºÏùÑ Î≥ÑÎèÑÏùò RowÎ°ú Î∂ÑÎ¶¨ÌïòÍ≥† Ï†ïÎ†¨ */}
            <div className={styles.metaRow}>
              <div className={styles.metaInfo}>
                <span className={styles.author}>ÏûëÏÑ±Ïûê: {post.authorId}</span>
                <span className={styles.date}> Îì±Î°ùÏùº:
                                {new Date(post.createdAt).toLocaleDateString('ko-KR')}
                            </span>
                <span className={styles.views}> Ï°∞ÌöåÏàò: {post.viewCount}</span>
              </div>

              {isAuthor && (
                  <div className={styles.authorActions}>
                    <button onClick={handleEdit} className={styles.editButton}>
                      ÏàòÏ†ï
                    </button>
                    <button
                        onClick={handleDelete}
                        disabled={isDeleting}
                        className={styles.deleteButton}
                    >
                      {isDeleting ? 'ÏÇ≠Ï†ú Ï§ë...' : 'ÏÇ≠Ï†ú'}
                    </button>
                  </div>
              )}
            </div>

            {(tags ?? []).length > 0 && (
                <div className={styles.tagsContainer}>
                  {tags.map((tag, index) => (
                      <span
                          key={index}
                          className={styles.tag}
                          onClick={() => router.push(`/tag/${encodeURIComponent(tag)}`)}
                          style={{ cursor: 'pointer' }}
                          data-color={index % 5}
                      >
                                    #{tag}
                                </span>
                  ))}
                </div>
            )}
          </header>

          {post.thumbnail && (
              <img
                  src={post.thumbnail}
                  alt={post.title}
                  className={styles.postThumbnail}
                  onError={(e) => (e.currentTarget.style.display = 'none')}
              />
          )}

          {/* ‚úÖ [ÏàòÏ†ï] Î©îÏù∏ ÏΩòÌÖêÏ∏†, ÎåìÍ∏Ä, ÏñëÏ™Ω ÏÇ¨Ïù¥ÎìúÎ∞îÎ•º Î™®Îëê Ìè¨Ìï®ÌïòÎäî Grid Ïª®ÌÖåÏù¥ÎÑà */}
          <div className={styles.gridWrapper}>

            {/* ‚¨ÖÔ∏è 1. ÏôºÏ™Ω ÏÇ¨Ïù¥ÎìúÎ∞î (Ï¢ãÏïÑÏöî/Í≥µÏú† - Í≥†Ï†ï) */}
            <aside className={styles.leftSidebar}>

              <div className={styles.stickyBox}>
                <LikeShareSidebar
                    postId={post.postId}/>
              </div>
            </aside>

            {/* ‚û°Ô∏è 2. Ï§ëÏïô ÏΩòÌÖêÏ∏† ÏòÅÏó≠ (Í≤åÏãúÍ∏Ä Î≥∏Î¨∏ + ÎåìÍ∏Ä) */}
            {/* ÎåìÍ∏Ä ÏÑπÏÖòÏùÑ Î©îÏù∏ ÏΩòÌÖêÏ∏†ÏôÄ Í∞ôÏùÄ ÎÑàÎπÑÎ°ú ÌÜµÏùº */}
            <div className={styles.centralContent}>
              <article className={styles.mainContent}>
                <PostContent content={post.content} />
              </article>

              {/* ‚úÖ [Ïù¥Îèô/ÌÜµÌï©] CommentSectionÏù¥ Ï§ëÏïô ÏΩòÌÖêÏ∏† ÎÑàÎπÑÎ•º Îî∞Î•¥ÎèÑÎ°ù Ïù¥Í≥≥Ïóê Î∞∞Ïπò */}
              <CommentSection postId={post.postId} comments={post.comments || []} />
            </div>

            {/* ‚û°Ô∏è 3. Ïò§Î•∏Ï™Ω Î™©Ï∞® (Ï°∞Í±¥Î∂Ä Î†åÎçîÎßÅ Î∞è Í≥†Ï†ï) */}
            {shouldShowToc && (
                <aside className={styles.sidebar}>
                  <div className={styles.stickyBox}>
                    <TableOfContents content={post.content} />
                  </div>
                </aside>
            )}
          </div>
        </div>
      </>
  );
}