export const getServerSideProps: GetServerSideProps = async (context) => {
  const { postId } = context.params!;

  try {
    const post = await readPost(Number(postId));
    return {
      props: { post },
    };
  } catch (error) {
    console.error("Í≤åÏãúÎ¨º Î°úÎìú Ïã§Ìå®:", error);
    return {
      props: { post: null },
    };
  }
};