'use client';

import { useState, useEffect } from 'react';
import styles from '@/styles/blogService/commentSection.module.css';
import {
  createComment,
  updateComment,
  deleteComment,
  getCommentCount,
  getCommentsByPostId,
} from '@/api/blogService/comment';
import { CommentDTO } from '@/types/blogService/blogType';
import React from 'react'; // React import Ï∂îÍ∞Ä

interface CommentSectionProps {
  postId: number;
  comments?: CommentDTO[]; // SSRÏö©
}

// ÎÇ†Ïßú Ìè¨Îß∑ÌåÖ Ïú†Ìã∏Î¶¨Ìã∞
const formatDate = (dateString: string) => {
  return new Date(dateString).toLocaleDateString('ko-KR', {
    year: 'numeric',
    month: 'long',
    day: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
  });
};

// üü¢ [ÏàòÏ†ï ÏôÑÎ£åÎêú] Îì§Ïó¨Ïì∞Í∏∞, ÎåÄÎåìÍ∏Ä Ï†ëÍ∏∞/ÌéºÏπòÍ∏∞ Í∏∞Îä•ÏùÑ Ï∂îÍ∞ÄÌïú CommentItem Ïª¥Ìè¨ÎÑåÌä∏
const CommentItem = ({
                       comment,
                       currentUserSignId,
                       replyingTo,
                       setReplyingTo,
                       editingCommentId,
                       setEditingCommentId,
                       onReply,
                       onEdit,
                       onDelete,
                       isLoading,
                       depth = 0 // üí° depth prop Ï∂îÍ∞Ä (Í∏∞Î≥∏Í∞í 0)
                     }: {
  comment: CommentDTO;
  currentUserSignId: string;
  replyingTo: number | null;
  setReplyingTo: (id: number | null) => void;
  editingCommentId: number | null;
  setEditingCommentId: (id: number | null) => void;
  onReply: (parentId: number, content: string) => void;
  onEdit: (id: number, content: string) => void;
  onDelete: (id: number) => void;
  isLoading: boolean;
  depth?: number; // üí° ÌÉÄÏûÖ Ï†ïÏùò Ï∂îÍ∞Ä
}) => {
  // ÏûÖÎ†• ÏÉÅÌÉú Í¥ÄÎ¶¨
  const [localReplyContent, setLocalReplyContent] = useState('');
  const [localEditContent, setLocalEditContent] = useState('');

  // const [isRepliesVisible, setIsRepliesVisible] = useState(depth === 0);
  const [isRepliesVisible, setIsRepliesVisible] = useState(false);
  useEffect(() => {
    // ÏàòÏ†ï ÌèºÏù¥ Ïó¥Î¶¥ ÎïåÎßå ÌòÑÏû¨ ÎåìÍ∏Ä ÎÇ¥Ïö©ÏùÑ localEditContentÏóê ÏÑ§Ï†ï
    if (editingCommentId === comment.commentId) {
      setLocalEditContent(comment.content);
    }
  }, [editingCommentId, comment.commentId, comment.content]);

  // ÏÇ≠Ï†ú Ìï∏Îì§Îü¨ (API Ìò∏Ï∂ú Î∞è ÏÉÅÌÉú ÏóÖÎç∞Ïù¥Ìä∏)
  const handleDeleteComment = async (id: number) => {
    if (!confirm('Ï†ïÎßêÎ°ú Ïù¥ ÎåìÍ∏ÄÏùÑ ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?')) return;
    // setIsLoadingÏùÄ CommentSectionÏóêÏÑú Í¥ÄÎ¶¨ÌïòÍ≥† ÏûàÏúºÎØÄÎ°ú Ïó¨Í∏∞ÏÑúÎäî ÏÇ¨Ïö©ÌïòÏßÄ ÏïäÏäµÎãàÎã§.
    // CommentSectionÏùò handleDeleteÏóêÏÑú setIsLoadingÏùÑ Ï≤òÎ¶¨ÌïòÎèÑÎ°ù Î°úÏßÅ Î≥ÄÍ≤Ω
    try {
      // API Ìò∏Ï∂ú (ÏÑ±Í≥µ/Ïã§Ìå®Îßå ÌåêÎã®)
      const res = await deleteComment(id, currentUserSignId);
      if (res.includes('Í∂åÌïú')) return alert(res);
      // ÏÉÅÏúÑ Ïª¥Ìè¨ÎÑåÌä∏Ïùò Ìä∏Î¶¨ ÏÇ≠Ï†ú Î°úÏßÅ Ìò∏Ï∂ú
      onDelete(id);
    } catch {
      alert('ÏÇ≠Ï†ú Ïã§Ìå®');
    }
  };

  // ÎãµÍ∏Ä ÏûëÏÑ± Ìï∏Îì§Îü¨
  const handleReplySubmit = () => {
    if (localReplyContent.trim() === '') {
      alert('ÎãµÍ∏Ä ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
      return;
    }
    // ÎãµÍ∏Ä ÏûëÏÑ± ÌõÑ, Î∞©Í∏à ÏûëÏÑ±Ìïú ÎãµÍ∏ÄÏùÑ Î≥¥Í∏∞ ÏúÑÌï¥ isRepliesVisibleÏùÑ trueÎ°ú ÏÑ§Ï†ï
    onReply(comment.commentId, localReplyContent);
    setReplyingTo(null);
    setLocalReplyContent('');
    setIsRepliesVisible(true); // ÎãµÍ∏Ä ÏûëÏÑ± ÌõÑ ÌéºÏπòÍ∏∞
  };

  // ÏàòÏ†ï Ï†úÏ∂ú Ìï∏Îì§Îü¨
  const handleEditSubmit = () => {
    if (localEditContent.trim() === '') {
      alert('ÏàòÏ†ï ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
      return;
    }
    // üí° onEdit Ìò∏Ï∂ú: TS2304 Ïò§Î•ò Ìï¥Í≤∞Ïùò ÌïµÏã¨
    onEdit(comment.commentId, localEditContent);
    // ÏàòÏ†ï ÏôÑÎ£åÎäî ÏÉÅÏúÑ Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Î°úÎî© ÏôÑÎ£å ÌõÑ Ï≤òÎ¶¨ÌïòÎäî Í≤ÉÏù¥ Îçî Ï¢ãÏúºÎÇò,
    // Ïó¨Í∏∞ÏÑúÎäî Î°úÎî© ÏãúÏûë Ï¶âÏãú ÌèºÏùÑ Îã´ÏäµÎãàÎã§. (CommentSectionÏóêÏÑú Ï≤òÎ¶¨)
    // setEditingCommentId(null);
  };

  const handleCancelEdit = () => {
    setEditingCommentId(null);
    setLocalEditContent('');
  };

  // üí° [ÏàòÏ†ï] ÎåìÍ∏Ä Ïª®ÌÖåÏù¥ÎÑàÏóê CSS Î≥ÄÏàò(--depth)Î•º ÏÇ¨Ïö©ÌïòÏó¨ Îì§Ïó¨Ïì∞Í∏∞Î•º Ï†ÅÏö©Ìï©ÎãàÎã§.
  return (
      <div
          className={`${styles.commentItem} ${depth > 0 ? styles.replyItem : ''}`} // ÎåÄÎåìÍ∏ÄÏùÄ replyItem ÌÅ¥ÎûòÏä§ Ï∂îÍ∞Ä
          style={{ '--depth': depth } as React.CSSProperties} // CSS Î≥ÄÏàò Ï†ÑÎã¨
      >
        <div className={styles.commentHeader}>
          <div className={styles.commentAvatar}>{comment.userId[0].toUpperCase()}</div>
          <div className={styles.info}>
            <span className={styles.commentAuthor}>{comment.userId}</span>
            <span className={styles.commentDate}>{formatDate(comment.createdAt)}</span>
          </div>
        </div>

        {editingCommentId === comment.commentId ? (
            <div className={styles.editForm}>
          <textarea
              value={localEditContent}
              onChange={e => setLocalEditContent(e.target.value)}
              rows={3}
              className={styles.commentTextarea}
              disabled={isLoading}
          />
              <div className={styles.actionButtons}>
                <button
                    onClick={handleEditSubmit}
                    className={styles.submitButton}
                    disabled={isLoading || localEditContent.trim() === ''}
                >
                  {isLoading ? 'ÏàòÏ†ï Ï§ë...' : 'ÏàòÏ†ï ÏôÑÎ£å'}
                </button>
                <button
                    onClick={handleCancelEdit}
                    className={styles.cancelButton}
                    disabled={isLoading}
                >
                  Ï∑®ÏÜå
                </button>
              </div>
            </div>
        ) : (
            <p className={styles.commentContent}>{comment.isDeleted ? '(ÏÇ≠Ï†úÎêú ÎåìÍ∏ÄÏûÖÎãàÎã§)' : comment.content}</p>
        )}

        {/* Ïï°ÏÖò Î≤ÑÌäºÎì§ */}
        {!comment.isDeleted && editingCommentId !== comment.commentId && (
            <div className={styles.commentActions}>
              <button
                  onClick={() => setReplyingTo(comment.commentId)}
                  className={styles.replyButton}
                  disabled={isLoading}
              >
                ÎãµÍ∏Ä
              </button>
              {currentUserSignId === comment.userId && (
                  <>
                    <button
                        onClick={() => setEditingCommentId(comment.commentId)}
                        className={styles.editButton}
                        disabled={isLoading}
                    >
                      ÏàòÏ†ï
                    </button>
                    <button
                        onClick={() => onDelete(comment.commentId)}
                        className={styles.deleteButton}
                        disabled={isLoading}
                    >
                      ÏÇ≠Ï†ú
                    </button>
                  </>
              )}
            </div>
        )}

        {/* üí° [ÏàòÏ†ï] ÎåÄÎåìÍ∏Ä Ï†ëÍ∏∞/ÌéºÏπòÍ∏∞ Î≤ÑÌäº (ÎåìÍ∏ÄÏù¥ ÏûàÍ≥†, ÏµúÏÉÅÏúÑ ÎåìÍ∏ÄÏù¥ ÏïÑÎãå Í≤ΩÏö∞ÏóêÎèÑ ÌëúÏãú Í∞ÄÎä•) */}
        {comment.replies.length > 0 && (
            <div className={styles.replyToggle} onClick={() => setIsRepliesVisible(prev => !prev)}>
              {isRepliesVisible ? 'ÎãµÍ∏Ä Ï†ëÍ∏∞ ‚ñ≤' : `ÎãµÍ∏Ä ${comment.replies.length}Í∞ú ÌéºÏπòÍ∏∞ ‚ñº`}
            </div>
        )}

        {/* ÎãµÍ∏Ä ÏûëÏÑ± Ìèº */}
        {replyingTo === comment.commentId && (
            <div className={styles.replyForm}>
          <textarea
              value={localReplyContent}
              onChange={e => setLocalReplyContent(e.target.value)}
              placeholder={`${comment.userId}ÎãòÍªò ÎãµÍ∏Ä ÏûëÏÑ±`}
              rows={2}
              className={styles.commentTextarea}
              disabled={isLoading}
          />
              <div className={styles.actionButtons}>
                <button
                    onClick={handleReplySubmit}
                    className={styles.submitButton}
                    disabled={isLoading || localReplyContent.trim() === ''}
                >
                  {isLoading ? 'ÏûëÏÑ± Ï§ë...' : 'ÎãµÍ∏Ä ÏûëÏÑ±'}
                </button>
                <button
                    onClick={() => setReplyingTo(null)}
                    className={styles.cancelButton}
                    disabled={isLoading}
                >
                  Ï∑®ÏÜå
                </button>
              </div>
            </div>
        )}

        {/* üí° [ÌïµÏã¨] ÎåÄÎåìÍ∏Ä Î™©Î°ù Î†åÎçîÎßÅ Ï°∞Í±¥: isRepliesVisibleÏù¥ trueÏùº ÎïåÎßå Î†åÎçîÎßÅ */}
        {comment.replies.length > 0 && isRepliesVisible && (
            <div className={styles.repliesList}>
              {comment.replies.map(r => (
                  // üí° [ÌïµÏã¨] Ïû¨Í∑Ä Ìò∏Ï∂ú Ïãú depthÎ•º 1 Ï¶ùÍ∞ÄÏãúÏºú Ï†ÑÎã¨
                  <CommentItem
                      key={r.commentId}
                      comment={r}
                      currentUserSignId={currentUserSignId}
                      replyingTo={replyingTo}
                      setReplyingTo={setReplyingTo}
                      editingCommentId={editingCommentId}
                      setEditingCommentId={setEditingCommentId}
                      onReply={onReply}
                      onEdit={onEdit}
                      onDelete={onDelete}
                      isLoading={isLoading}
                      depth={depth + 1} // üí° depth Ï¶ùÍ∞Ä
                  />
              ))}
            </div>
        )}
      </div>
  );
};


const updateTree = (list: CommentDTO[], id: number, fn: (c: CommentDTO) => CommentDTO): CommentDTO[] =>
    list.map(c =>
        c.commentId === id
            ? fn(c)
            : (c.replies && c.replies.length > 0)
                ? {
                  ...c,
                  replies: updateTree(c.replies, id, fn),
                }
                : c
    );

// Ìä∏Î¶¨ÏóêÏÑú ÎåìÍ∏ÄÏùÑ Ï†úÍ±∞ÌïòÎäî Ïú†Ìã∏ (ÎåÄÎåìÍ∏ÄÍπåÏßÄ Ï†úÍ±∞Îê† Í≤ΩÏö∞ Î∂ÄÎ™®Ïùò childCountÎèÑ ÏóÖÎç∞Ïù¥Ìä∏)
const removeFromTree = (list: CommentDTO[], id: number): CommentDTO[] =>
    list.reduce((acc, c) => {
      if (c.commentId === id) {
        return acc;
      }

      let newReplies = c.replies;
      if (c.replies && c.replies.length > 0) {
        newReplies = removeFromTree(c.replies, id);
      }
      acc.push({ ...c, replies: newReplies });
      return acc;
    }, [] as CommentDTO[]);


export default function CommentSection({ postId, comments: ssrComments = [] }: CommentSectionProps) {
  const [comments, setComments] = useState<CommentDTO[]>([]);
  const [commentCount, setCommentCount] = useState(0);
  const [currentUserSignId, setCurrentUserSignId] = useState('');
  const [newComment, setNewComment] = useState('');
  const [replyingTo, setReplyingTo] = useState<number | null>(null);
  const [editingCommentId, setEditingCommentId] = useState<number | null>(null);
  // üü¢ isLoading ÏÉÅÌÉú: TS2552 Ïò§Î•ò Ìï¥Í≤∞ÏùÑ ÏúÑÌï¥ Ï†ïÏùò
  const [isLoading, setIsLoading] = useState(false);

  // ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ÏóêÏÑúÎßå Ïã§Ìñâ ‚Üí Î¨¥Ï°∞Í±¥ ÏµúÏã† Îç∞Ïù¥ÌÑ∞ + replies Îß§Ìïë Î≥¥Ïû•
  useEffect(() => {
    const userId = typeof window !== 'undefined' ? localStorage.getItem('userSignId') || '' : '';
    setCurrentUserSignId(userId);

    const fetchData = async () => {
      setIsLoading(true); // üü¢ Î°úÎî© ÏãúÏûë
      try {
        const fetchedComments = await getCommentsByPostId(postId);
        const fetchedCount = await getCommentCount(postId);
        // console.log("üî• ÏÑúÎ≤ÑÏóêÏÑú Î∞õÏùÄ ÎåìÍ∏Ä Îç∞Ïù¥ÌÑ∞ (Î∞∞Ïó¥):", fetchedComments);
        // console.log("üî• ÏµúÏÉÅÏúÑ ÎåìÍ∏Ä Í∞úÏàò:", fetchedComments.length);
        setComments(fetchedComments);
        setCommentCount(fetchedCount);
      } catch(e) {
        console.error("ÎåìÍ∏Ä Îç∞Ïù¥ÌÑ∞ Î°úÎìú Ïã§Ìå®", e);
      } finally {
        setIsLoading(false); // üü¢ Î°úÎî© Ï¢ÖÎ£å
      }
    };
    fetchData();
  }, [postId]);

  // ÏµúÏÉÅÏúÑ ÎåìÍ∏Ä ÏûëÏÑ±
  const handleSubmitComment = async () => {
    if (newComment.trim() === '') {
      alert('ÎåìÍ∏Ä ÎÇ¥Ïö©ÏùÑ ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
      return;
    }
    setIsLoading(true); // üü¢ Î°úÎî© ÏãúÏûë
    try {
      const dto = { postId, parentCommentId: null, content: newComment };
      const newCmt = await createComment(currentUserSignId, dto);
      setComments(prev => [newCmt, ...prev]); // ÏµúÏã† ÎåìÍ∏ÄÏùÑ Îß® ÏúÑÏóê Ï∂îÍ∞Ä
      setCommentCount(c => c + 1);
      setNewComment('');
    } catch {
      alert('ÎåìÍ∏Ä ÏûëÏÑ± Ïã§Ìå®');
    } finally {
      setIsLoading(false); // üü¢ Î°úÎî© Ï¢ÖÎ£å
    }
  };

  // ÎãµÍ∏Ä ÏûëÏÑ± (CommentItem ÎÇ¥Î∂ÄÏóêÏÑú Ìò∏Ï∂ú)
  const handleReply = async (parentId: number, content: string) => {
    setIsLoading(true); // üü¢ Î°úÎî© ÏãúÏûë
    try {
      const dto = { postId, parentCommentId: parentId, content };
      const newCmt = await createComment(currentUserSignId, dto);

      // ÎãµÍ∏ÄÏù¥ Îã¨Î¶∞ Î∂ÄÎ™® ÎåìÍ∏ÄÏùÑ Ï∞æÏïÑ repliesÏóê Ï∂îÍ∞Ä
      setComments(prev => updateTree(prev, parentId, c => ({
        ...c,
        replies: [...c.replies, newCmt],
        childCount: c.childCount + 1,
      })));

      setCommentCount(c => c + 1);
    } catch {
      alert('ÎãµÍ∏Ä ÏûëÏÑ± Ïã§Ìå®');
    } finally {
      setIsLoading(false);
    }
  };

  const handleEdit = async (id: number, content: string) => {
    setIsLoading(true);
    try {
      const dto = { content };
      // API Ìò∏Ï∂ú
      const updatedCmt = await updateComment(id, currentUserSignId, dto);

      // ÎåìÍ∏Ä Î™©Î°ù ÏóÖÎç∞Ïù¥Ìä∏
      setComments(prev => updateTree(prev, id, c => ({
        ...c,
        content: updatedCmt.content,
        updatedAt: updatedCmt.updatedAt
      })));

      // ÏàòÏ†ï Ìèº Îã´Í∏∞
      setEditingCommentId(null);
    } catch (e: any) {
      alert(e.message || 'ÎåìÍ∏Ä ÏàòÏ†ï Ïã§Ìå®');
      console.error(e);
    } finally {
      setIsLoading(false); // üü¢ Î°úÎî© Ï¢ÖÎ£å
    }
  };

  // ÎåìÍ∏Ä ÏÇ≠Ï†ú (CommentItemÏóêÏÑú Ìò∏Ï∂ú)
  const handleDelete = async (id: number) => {
    setIsLoading(true);
    try {
      const res = await deleteComment(id, currentUserSignId);
      if (res.includes('Í∂åÌïú')) {
        alert(res);
        return;
      }

      const fetchedComments = await getCommentsByPostId(postId);
      const fetchedCount = await getCommentCount(postId);
      setComments(fetchedComments);
      setCommentCount(fetchedCount);

    } catch (e) {
      alert('ÎåìÍ∏Ä ÏÇ≠Ï†ú Ïã§Ìå®');
      console.error(e);
    } finally {
      setIsLoading(false);
    }
  };

  return (
      <div className={styles.commentSection}>
        <h2 className={styles.commentTitle}>{commentCount}Í∞úÏùò ÎåìÍ∏Ä</h2>

        <div className={styles.commentForm}>
        <textarea
            value={newComment}
            onChange={e => setNewComment(e.target.value)}
            placeholder="ÎåìÍ∏ÄÏùÑ ÏûëÏÑ±ÌïòÏÑ∏Ïöî"
            rows={4}
            className={styles.commentTextarea}
            disabled={isLoading}
        />
          <button
              onClick={handleSubmitComment}
              className={styles.submitButton}
              disabled={isLoading || newComment.trim() === ''}
          >
            {isLoading ? 'ÏûëÏÑ± Ï§ë...' : 'ÎåìÍ∏Ä ÏûëÏÑ±'}
          </button>
        </div>

        <div className={styles.commentList}>
          {comments.map(c => (
              <CommentItem
                  key={c.commentId}
                  comment={c}
                  currentUserSignId={currentUserSignId}
                  replyingTo={replyingTo}
                  setReplyingTo={setReplyingTo}
                  editingCommentId={editingCommentId}
                  setEditingCommentId={setEditingCommentId}
                  onReply={handleReply}
                  onEdit={handleEdit}
                  onDelete={handleDelete}
                  isLoading={isLoading}
                  depth={0}
              />
          ))}
        </div>
      </div>
  );
}