'use client';

import React, { useEffect, useState } from 'react';
import styles from '@/styles/blogService/postDetail.module.css';

interface TocItem {
  id: string;
  text: string;
  level: number;
}

export default function TableOfContents({ content }: { content: string }) {
  const [tocItems, setTocItems] = useState<TocItem[]>([]);

  // üí° [ÏàòÏ†ï] PostContentÍ∞Ä Î†åÎçîÎßÅÌïú Ïã§Ï†ú DOMÏóêÏÑú Ï†úÎ™© ÌÉúÍ∑∏Î•º Ïä§Ï∫îÌïòÏó¨ Î™©Ï∞®Î•º ÎßåÎì≠ÎãàÎã§.
  useEffect(() => {
    const scanHeadings = () => {
      const scannedItems: TocItem[] = [];
      // article.mainContent ÎÇ¥Î∂ÄÏùò H1, H2, H3 ÌÉúÍ∑∏ Ï§ë IDÍ∞Ä ÏûàÎäî Í≤ÉÏùÑ Ï∞æÏäµÎãàÎã§.
      document.querySelectorAll('article h1[id], article h2[id], article h3[id]').forEach((heading) => {
        const id = heading.getAttribute('id');
        const text = heading.textContent;
        const level = parseInt(heading.tagName[1], 10);

        if (id && text && text.trim().length > 0) {
          scannedItems.push({ id, text: text.trim(), level });
        }
      });
      setTocItems(scannedItems);
    };

    // DOMÏù¥ ÏôÑÏ†ÑÌûà ÏóÖÎç∞Ïù¥Ìä∏Îêú ÌõÑ Ïä§Ï∫îÌïòÎèÑÎ°ù Î≥¥Ïû• (PostContentÏùò innerHTML Ï†ÅÏö© ÌõÑ)
    const timeoutId = setTimeout(scanHeadings, 100);

    return () => clearTimeout(timeoutId);
  }, [content]);


  // üñ±Ô∏è Ïä§ÌÅ¨Î°§ Ïù¥Îèô Î°úÏßÅ: ÌÅ¥Î¶≠ Ïãú Î∂ÄÎìúÎüΩÍ≤å Ïù¥Îèô
  const handleScrollTo = (e: React.MouseEvent<HTMLAnchorElement>, id: string) => {
    e.preventDefault();
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({
        behavior: 'smooth',
        block: 'start',
      });
    }
  };


  // Î™©Ï∞®Í∞Ä ÏóÜÏúºÎ©¥ null Î∞òÌôò (PostDetail.tsxÏóêÏÑú Ïù¥ÎØ∏ Ï°∞Í±¥Î∂Ä Î†åÎçîÎßÅ ÌñàÏßÄÎßå ÏïàÏ†Ñ Ïû•Ïπò)
  if (tocItems.length === 0) {
    return null;
  }

  return (
      <nav className={styles.tocNav}>
        {/* üí° ÏöîÏ≤≠Ïóê Îî∞Îùº Î™©Ï∞® Ï†úÎ™© ÌÉúÍ∑∏ Ï∂îÍ∞Ä */}
        <h4 className={styles.tocTitle}>Î™©Ï∞®</h4>
        <ul className={styles.tocList}>
          {tocItems.map((item, index) => (
              <li
                  key={index}
                  // CSS Î™®ÎìàÏóê Ï†ïÏùòÎêú Î†àÎ≤®Î≥Ñ Îì§Ïó¨Ïì∞Í∏∞ ÌÅ¥ÎûòÏä§ ÏÇ¨Ïö© (Ïòà: tocLevel2, tocLevel3)
                  className={styles[`tocLevel${item.level}`]}
              >
                <a
                    href={`#${item.id}`}
                    className={styles.tocLink}
                    onClick={(e) => handleScrollTo(e, item.id)}
                >
                  {item.text}
                </a>
              </li>
          ))}
        </ul>
      </nav>
  );
}